.menu-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-top: 20%;
}


.back-btn {
  position: absolute;
  top: 20px;
  left: 20px;
  border: none;
  background-color: transparent;
  font-size: 1.2rem;
  color: #000;
}

.back-btn:hover {
  cursor: pointer;
  text-decoration: underline;
}



.menu-item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 150px;
  height: 150px;
  margin: 20px;
  font-size: 4vw;
  text-align: center;
  background-color: #f1f1f1;
  border-radius: 10px;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  text-decoration: none;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .menu-item {
    font-size: 2.5vw;
    width: 120px;
    height: 120px;
    margin: 10px;
  }
}

@media screen and (min-width: 1024px) {
  .menu-item {
    font-size: 1.5vw;
    width: 150px;
    height: 150px;
    margin: 20px;
  }
}

.menu-item:hover {
  transform: scale(1.1);
  background-color: #e0e0e0;
}

.menu-item.active {
  background-color: #2196f3;
  color: #ffffff;
}

.menu-item i {
  font-size: 6vw;
  margin-bottom: 5px;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .menu-item i {
    font-size: 3.5vw;
  }
}

@media screen and (min-width: 1024px) {
  .menu-item i {
    font-size: 2vw;
  }
}

.menu-item i:hover {
  transform: scale(1.1);
}

.menu-item span {
  font-size: 4vw;
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 115px;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .menu-item span {
    font-size: 1.2vw;
    max-width: 48px;
  }
}

@media screen and (min-width: 1024px) {
  .menu-item span {
    font-size: 1.8vw;
    max-width: 93px;
  }
}
