.add-product-form {
  border: 2px solid #ccc;
  padding: 20px;
  width: 80%;
  margin: 0 auto;
  text-align: left;
}

.add-product-form h2 {
  margin-top: 0;
}

.add-product-form label {
  display: block;
  margin-bottom: 10px;
}

.add-product-form select {
  width: 100%;
  height: 100px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.add-product-form input[type="text"],
.add-product-form input[type="number"] {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-bottom: 10px;
}

.add-product-form button {
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

.add-product-form button:hover {
  background-color: #2e8b57;
}

.product-card {
  margin-bottom: 20px;
  border-radius: 8px;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.2);
}

.product-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #eee;
}

.product-name {
  font-size: 18px;
  font-weight: 600;
}

.product-cost {
  font-size: 16px;
  font-weight: 600;
  color: #007bff;
}

.product-body {
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.product-materials {
  width: 100%;
  margin-bottom: 20px;
}

.materials-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.product-buttons {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 20px;
}

@media only screen and (min-width: 768px) {
  .product-card {
    flex-basis: calc(33.33% - 20px);
    margin-right: 20px;
    margin-bottom: 0;
  }

  .product-body {
    flex-basis: calc(66.66% - 20px);
  }
}
